.d-flex{
    display: flex;
}

.font-1{
    font-size: 16px;
    line-height: 24px;
}

.font-2{
    font-size: 12px;
    line-height: 24px;
}

.font-3{
    font-size: 14px;
    line-height: 21px;
}

.text-normal{
    color: black;
}

// 圖片取代文字
// .logo a{
//     background:url('logo圖片路徑') no-repeat;
// }

.hide-text{
    text-indent:101%;  /*首行縮排*/
    overflow:hidden;   /*自動隱藏超出的文字或圖片*/
    white-space:nowrap; /*希望元素在第一排上面，沒有斷行*/ 
}


/* 背景致中填滿 */
.bg-cover{
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

// 清除浮動
.clearfix{
    overflow: hidden;
}

